import { useContext } from "react";
import { StepsContext } from "../../../../context/ContextProv";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Radio from "@mui/material/Radio";
import TextField from "@mui/material/TextField";
import Typography from "@mui/material/Typography";
import TextInputsErrors from "./InputsErrors";
import HeadingPrimary from "../../../../components/HeadingPrimary";
import Box from "@mui/material/Box";

const InputsMembership = () => {
  const { formik } = useContext(StepsContext);

  return (
    <FormControl sx={{ width: "100%" }}>
      <HeadingPrimary
        headingText={"Przynaleno do innych zwizkw zawodowych:"}
      />

      <RadioGroup
        name="membership"
        value={formik.values.membership}
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        sx={{
          gap: 3,
          width: { xs: "80%", sm: "60%" },
          marginInline: "auto",
          ml: { xs: "auto", sm: "40%" },
          mt: { xs: 2, sm: 5 },
        }}
      >
        {[
          "Nie nale do innego zwizku zawodowego u pracodawcy.",
          "Nale do innego zwizku zawodowego u pracodawcy, jednak chc by przy podawaniu informacji o liczbie czonkw moje czonkowstwo uwzgdnia wycznie OZZIP.",
          "Nale do inngeo zwizku zawodowego u pracodawcy i nie chc by przy podawaniu informacji i liczbie czonkw OZZIP uwzgldnia moje czonkostwo.",
        ].map(membership => {
          return (
            <FormControlLabel
              key={membership}
              value={membership}
              control={<Radio />}
              label={membership}
              sx={{
                ml: -1,
                "& .MuiFormControlLabel-label": {
                  mr: 1,
                  color: "info.dark",
                  fontSize: theme => theme.typography.fs_16_rg,
                },
              }}
            />
          );
        })}
      </RadioGroup>
      <Box
        sx={{
          width: { xs: "80%", sm: "60%" },
          marginInline: "auto",
          marginLeft: { xs: "auto", sm: "40%" },
          mt: 1,
        }}
      >
        <TextInputsErrors formik={formik} value={"membership"} />
      </Box>
    </FormControl>
  );
};

export default InputsMembership;
