import Typography from "@mui/material/Typography";
import { styled } from "@mui/material/styles";
import { useTheme } from "@mui/material/styles";

const StepConfirmationParagraphs = () => {
 const theme = useTheme();

  const StyledParagraph = styled(Typography)(({ theme }) => ({
    // color: theme.palette.info.dark,
    // fontSize: theme.typography.h1,
    variant: theme.typography.fs_16_rg.variant,
    color: "info.dark",
    component: "p",
  }));

  const typographyProps = {
    variant: theme.typography.fs_16_rg.variant,
    color: "info.dark",
    component: "p",
  };

  return (
    <>
      <Typography {...typographyProps} sx={{ marginBlock: { xs: 3 } }}>
        Jeeli nasza Komisja nie postanowi inaczej, dat przyjcia Ciebie do
        zwizku bdzie:
      </Typography>
      <Typography {...typographyProps}>
        <strong> 1) </strong> ostatni dzie miesica, w ktrym owiadczenie to
        zostao dorczone:
      </Typography>
      <Typography {...typographyProps} sx={{ ml: 3, marginBlock: 1.6 }}>
        <strong>a)</strong> czonkowi prezydium organizacji podstawowej Zwizku
        (rwnie poprzed odebranie ze skrzynki pocztowej na tablicy zwizkowej)
        <br />
        <strong>b)</strong> bezporednio osobie dziaajcej z upowanienia
        prezydium Komisji <br />
        <strong>c)</strong> na ujawniony w KRS adres Zwizku (aktualnie: ul.
        Kocielna 4/1a, 60-538 Pozna)
        <br />
      </Typography>
      <Typography {...typographyProps}>
        <strong>2)</strong> dzie, w ktrym po otrzymaniu tego owiadczenia
        prezydium Komisji podjo si Twojej obrony wobec podmiotu
        zatrudniajcego (w tym pracodawcy).
      </Typography>
    </>
  );
};

export default StepConfirmationParagraphs;
