import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import HeadingPrimary from "../../../components/HeadingPrimary";
import ImgConfirmation from "../../../images/ImgConfirmation";
import StepConfirmationBanksList from "./StepConfirmationBanksList";
import StepConfirmationFooter from "./StepConfirmationFooter";
import StepConfirmationParagraphs from "./StepConfirmationParagraphs";

const StepConfirmation = () => {
  return (
    <Box>
      <HeadingPrimary headingText={"Deklaracja zostaa zoona"} />

      <Box sx={{ display: "grid", placeItems: "center", marginBlock: 8 }}>
        <ImgConfirmation />
      </Box>

      <HeadingPrimary
        headingText={"INFORMACJA O DACIE PRZYJCIA DO ZWIZKU"}
        color={"info.dark"}
      />

      <StepConfirmationParagraphs />
      <Typography
        variant="fs_12_rg"
        color="info.dark"
        component="p"
        sx={{ marginBlock: { xs: 3 } }}
      >
        Powysze wynika z Uchway nr 10 Krajowego Zjazdu Dlegatw i Dlelegatek
        OZZIP z 12.03.2022 r
      </Typography>

      <HeadingPrimary
        headingText={
          "DROG E-MAIL OTRZYMASZ POTWIERDZENIE ZAPISANIA DO ZWIZKU ORAZ DODANIA DO LISTY DYSKUSYJNEJ"
        }
        color={"info.dark"}
      />

      <Box sx={{ mt: 5, mb: 10 }}>
        <HeadingPrimary
          headingText={
            "Przystpienie do komisji zostanie zweryfikowane po wykonaniu przelewu bankowego pierwszej skadki czonkowskiej na konto:"
          }
        />
        <HeadingPrimary
          headingText={
            "28 2130 0004 2001 0577 6570 0014 <br> OZZ Inicjatywa Pracownicza ul. Kocielna 4, 60-538 Pozna."
          }
        />
        <HeadingPrimary
          headingText={
            "Tytu przelweu: (imi i nazwisko), skadka za (miesic/kwarta)."
          }
        />
      </Box>
      <StepConfirmationBanksList />
      <StepConfirmationFooter />
    </Box>
  );
};

export default StepConfirmation;
